import { sendComment } from '@/api/course'
export default {
  props:{
    courseId:{
      type:Number,
      required:true
    }
  },
  data:()=>(
    {
      show:false,
      title:'课程已结束',
      score:0,
      content:''
    }
  ),
  methods:{
    toggle(autoCilck){
      if (!this.show) {
        this.title = autoCilck ? '课程已结束' : '课程评星'
      }
      //关闭弹窗处理
      if(this.show) {
        this.$emit('setIsEnd', false)
      }
      this.show = !this.show
      //this.$emit('getComment')
      
    },

    async sendComment(){
      if (!this.score) {
        return this.$toast.fail("请评分!")
      }
      if (!this.content) {
        return this.$toast.fail("请输入评论内容!")
      }
      try {
        let {data:{code}} = await sendComment({courseId:this.courseId, content:this.content, score:this.score})
        if(code === 0){
          //this.$notify('评论成功')
          this.$toast.success("评论成功")
          this.$emit('getComment')
        }
      } catch (error) {
        this.$notify('评论失败')
        console.log(error)
      }
      this.show = !this.show
    }
  }
}
