import { getForumFirstComment, submitForumComment, getForumSecondComment } from '@/api/course'
export default {
  props:{
    data:{
      type:Object,
      required:true
    }
  },
  data () {
    return {
      forumList:[],
      forumSum:0,
      placeholder:'发布讨论...',
      replyContent:'',
      replyInfo:{},
      forumTotal:0,
    }
  },
  methods:{
    // 获取讨论区一级评论
    async getForumFirstComment(params={page:1}, move){
      let { page } = params
      let { data:{code, data:list} } = await getForumFirstComment({courseId:this.data.courseId, page, pageSize:20})
      if(code === 0){
        let entries = list.entries
        // 获取对应的二级回复
        entries.forEach(async element => {
          let i = -1
          if(this.forumList.length > 0){
            i = this.forumList.findIndex(item=>item.id === element.id)
          }
          if(element.replies > 0){
            element['children'] = []
            // 保留当前的收起/展示状态
            element['childrenStatus'] = i !== -1 ? this.forumList[i].childrenStatus : false
            let { data:{ code, data:childrenList } } = await getForumSecondComment({parentId:element.id, courseId:this.data.courseId})
            if(code === 0){
              element['children'] = childrenList.entries
            }
          } 
        })
        this.forumList =  move ? [...this.forumList, ...entries] : [...entries]
        this.forumSum = list.sum
        this.forumTotal = list.total
      }
    },
    // 提交讨论区评论
    async submitForumComment(params){
      let { data:{code} } = await submitForumComment(params)
      if(code === 0){
        this.$toast({
          type:'success',
          message: params.parentId ? '回复成功' : '发布成功'
        })
        this.clearReply()
        this.getForumFirstComment()
      }
    },
    reply(info){
      this.placeholder =  "回复" + info.userName
      this.replyInfo = info
      this.$refs.reply.focus()
    },
    // 发布讨论
    async publishReply(){
      this.$refs.reply.focus()
      if(!this.replyContent){
        this.$notify('内容不能为空')
        return
      }
      let params = {
        content:this.replyContent,
        topticId:this.data.courseId,
        parentId:this.replyInfo.parentId || this.replyInfo.id || 0,
        replyId:this.replyInfo.id || 0,

      }
      this.submitForumComment(params)
    },
    // 切换讨论区回复状态
    toggleChildrenStatus(index, item){
      let copyForumList = [...this.forumList]
      copyForumList[index].childrenStatus = !item.childrenStatus
      this.forumList = copyForumList
    },
    // 重置讨论数据
    clearReply(){
      this.replyContent = ''
      this.replyInfo = {}
      this.placeholder = '发布讨论...'
    },
    // 加载更多讨论区数据
    more(){
      if(this.forumTotal - this.forumList.length > 0){
        this.getForumFirstComment({page:this.forumList.length / 20 + 1}, true)
      }
    },
  }
}
