import xss from "xss" // https://github.com/leizongmin/js-xss/blob/master/README.zh.md
const whiteList = xss.getDefaultWhiteList()
const handleTagAttr = (tag, name, value) => {
  if (name === "style") {
    return `${name}="${value}"`
  }
}
const options = {
  whiteList,
  onTagAttr: handleTagAttr
}
const xssDefense = new xss.FilterXSS(options)

export default xssDefense
