import { getCommentList, getCourseDetail } from '@/api/course'
import CommentPop from '@/components/Common/CommentPop.vue'
export default {
  components: {
    CommentPop
  },
  props:{
    data:{
      type:Object,
      required:true
    }
  },
  data() {
    return {
      courseCommentTotal:0,
      courseCommentList:[],
      courseCommentPage:1,
    }
  },
  methods:{
    setIsEnd() {
      this.$emit('setIsEnd', false)
    },
    // 我要评论
    sendComment(){
      this.$emit('setIsEnd', true)
      this.$refs.comment.toggle(false)
    },
    // 获取评论
    async getCommentList(){
      let { data:{code, data} } = await getCommentList({courseId:this.data.courseId, page:this.courseCommentPage, pageSize:20})
      if(code === 0){
        this.courseCommentList = this.courseCommentPage === 1 ? data.entries : [...this.courseCommentList, ...data.entries]
        this.courseCommentTotal = +(data.total)
      }
    },
    //评论后刷新页面
    updateComment() {
      this.$emit('setIsEnd', false)
      this.courseCommentPage = 1
      this.getCommentList()
      getCourseDetail(this.data.courseId).then(res =>{
        if (res.data.code === 0){
          this.$set(this.data, 'score', res.data.data.score)
        }
      })
    },
    //评论列表下拉加载
    showMoreComment() {
      this.courseCommentPage += 1
      this.getCommentList()
    }
  }
}
