import xssDefense from "@/utils/xssConfig"
import { secondsChangeFormat, storage } from 'ais-common-utils'
import { courseDownlaod } from "@/api/course"
import config from "@/config/common.config"
import CourseCard from '@/components/Common/CourseCard'
import CourseEvaluate from '@/components/CourseDetail/CourseEvaluate'
import CourseClock from '@/components/CourseDetail/CourseClock'
import CourseDiscussion from '@/components/CourseDetail/CourseDiscussion'
//import CourseLastBuy from '@/components/CourseDetail/CourseLastBuy'
export default {
  components: {
    CourseCard,
    CourseEvaluate,
    CourseDiscussion,
    //CourseLastBuy,
    CourseClock
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        lives:[],
        livesObj: {}
      })
    },
    tops: {
      type: Array,
      default:() => {
        []
      }
    },
    isShowPushCourse:{  // 是否展示推荐课程图标
      type:Boolean,
      default:false
    },
    isVideoWeChat:{ //微信小程序端页面适配
      type:Boolean,
      default:false
    }
  },
  data () {
    return {
      active: 0,
      sticky:false,
      activeArr:[]
    }
  },
  mounted(){
    this.sticky = true
    //路由带参数切tab菜单
    if(this.$route.fullPath.indexOf('/courseDetail')>-1 && this.$route.query.activeKey) {
      this.active = +this.$route.query.activeKey
      //this.getCommentList()
      this.$nextTick(() =>{
        this.$refs.CourseEvaluate.getCommentList()
      })
    }
  },
  methods: {
    //跳登录
    goLogin (url) {
      if(!storage.get('userStatus')){
        window.location.href = `${config.POST.school_h5}/school/passport/login?mobile=1&state=${url}`
        return false
      }
    }, 

    // 验证课程是否已经购买或者已经报名
    verifyCourseStatus(item){
      //没有报名或者没有购买，有试听的跳转试听，否则付费直接弹出订单确认页，免费就直接弹提示
      if(!this.data.apply) {
        if((item.previewTime > 0 || item.previewTime === -1) && item.scene === 1) {
          this.$router.push({
            path: `/videoDetail/${item.id}?isPreview=true`
          })
          return false
        }else {
          if(this.data.price > 0){
            //小程序，未购买直接跳小程序订单页
            if(this.isVideoWeChat && !this.data.apply ) {
              window.wx.miniProgram.navigateTo({
                url:`/pages/courseOrder/courseOrder?id=${this.data.courseId}`
              })
              return
            }
            //未登录，点击购买需跳转登录
            if (!this.data.apply) {
              const url = encodeURIComponent(this.$route.query.inviterId ? `${config.LINK.school_pc}/courseOrder/${this.data.courseId}?inviterId=${this.$route.query.inviterId}` : `${config.LINK.school_pc}/courseOrder/${this.data.courseId}`)
              this.goLogin(url)
            }
            this.$router.push({
              path: `/courseOrder/${this.data.courseId}`
            })
            return false
          }else {
            this.$toast.fail('请先报名课程!')
            return false 
          }
        }
      }
      return true
    },
    changeListPath(item){
      if(!this.verifyCourseStatus(item)) return
      const currentTime = parseInt((new Date()).getTime()/1000)
      //直播已结束不跳转
      if (item.scene !== 1 && currentTime >item.end) {
        return false
      }
      //直播未开始不跳转
      if (item.scene !== 1 && !((item.liveStatus ===1 || (item.end > currentTime > item.start)) || ((item.start-currentTime)<900) && item.liveStatus ===0)) {
        return this.$toast.fail('讲师还没有开课!')
      }
      // 判断是否是免费的直播
      this.$router.push(`/videoDetail/${item.id}`)
    },
    // 修改tab栏
    changeActive (val) {
      this.active = val
      if(val === 4 && !this.activeArr.includes(val)) {
        //this.getCommentList()
        this.$nextTick(() =>{
          this.$refs.CourseEvaluate.getCommentList()
        })
      }
      const top = document.getElementById('courseBottom').offsetTop
      document.documentElement.scrollTop = top - 25
      document.body.scrollTop = top - 25
      if(val === 3){
        this.footer = false
        //this.getForumFirstComment()
        if(!this.activeArr.includes(val)) {
          this.$nextTick(() =>{
            this.$refs.CourseDiscussion.getForumFirstComment()
          })
        }
        this.$nextTick(() =>{
          this.$refs.CourseDiscussion.clearReply()
        })
      }else{
        this.footer = true
        //this.clearReply()
      }
      if(!this.activeArr.includes(val)) {
        this.activeArr.push(val)
      }
    },
    // 课程介绍
    xssDefenseHand (content) {
      return xssDefense.process(content)
    },
    // 课程时间
    timeFnHand (item){
      if(item.scene === 1){
        const { h, m, s } = secondsChangeFormat(item.duration)
        if (Number(h) > 0) return `${h}:${m}:${s}(录播)`
        return `${m}:${s}(录播)`
      }else {
        const nowTime = parseInt((new Date()).getTime()/1000)
        let str = `${this.$changeTime(item.start)}`
        if(item.liveStatus === 1 || ((nowTime > item.start) && (item.end > nowTime))){
          str = str + '(直播中)'
        }else if(nowTime > item.end){
          str = str + '(直播已结束)'
        }else if((item.start - nowTime)>0){
          str = str + ( 0<(item.start - nowTime) && (item.start - nowTime)<900 ? '(直播即将开始)': '直播')
        }
        return str
      }
    },
    //系列课子课程试听
    previewClick(id) {
      this.$router.push({
        path: `/videoDetail/${id}?isPreview=true`
      })
    },
    // 训练营课件下载
    async downLoadCourse(item){
      //未登录，点击购买需跳转登录
      const url = encodeURIComponent(`${config.LINK.school_h5}/courseDetail/${this.data.courseId}`)
      this.goLogin(url)
      if (!this.data.apply) {
        return this.data.price === 0 ? this.$toast.fail('请先报名课程!') : this.$router.push({path: `/courseOrder/${this.data.courseId}`})
      }
      let {data:{code, data:list}} = await courseDownlaod({courseId: this.data.courseId, liveId :item.id})
      if(code === 0){
        window.location.href=list.document
      }
    }
  }
}
