import { getClockRecordList, getClockComment, getCourseClockInfo, applyFreeCourse } from '@/api/course'
import config from "@/config/common.config"
import { clockRole } from '@/config/mock'
import { changeImgurl  } from 'ais-common-utils'
export default {
  props:{
    data:{
      type:Object,
      required:true
    }
  },
  data() {
    return{
      clockPage:[],
      clockTotal:0,
      clockList:[],
      clockType:1, //0:最新打卡，1：精华打卡
      clockDay:0, //今日打卡数
      clockRole,
      clockVisible:false,
      config,
      images:[],
      currentTime: parseInt((new Date()).getTime()/1000), //获取当前时间
    }
  },
  created() {
    this.getCourseClockInfo()
    this.getClockRecordList()
  },
  methods:{
    //打卡切换
    clockSwitch() {
      this.clockType = this.clockType === 1 ? 0 : 1
      this.clockPage = 1
      this.getClockRecordList()
    },
    changePic(pic) {
      return pic ? (pic.includes(',') ? pic.split(',') : [pic]) :[]
    },
    //获取课程打卡详情
    async getCourseClockInfo() {
      const { data } = await getCourseClockInfo(this.data.courseId)
      if(data.code === 0) {
        this.clockDay = data.data.dayClock
      }
    },
    //获取打卡评论区列表
    async getClockRecordList() {
      const { data } = await getClockRecordList(this.data.courseId, {page:this.clockPage, pageSize:10, type:this.clockType})
      if(data.code === 0) {
        this.clockList = this.clockPage === 1 ? data.data.entries : [...this.clockList, ...data.data.entries]
        this.clockTotal = +data.data.total
      }
    },
    //获取打卡学员评论列表
    async getClockCommentList(index, row, flag=true) {
      if(flag) {
        const page = (row.page || 0) + 1
        const { data } = await getClockComment(row.id, {page:page, pageSize:100})
        if (data.code === 0) {
          const commentList = page === 1 ? data.data.entries : [...row.commentList, ...data.data.entries]
          this.$set(this.clockList, index, {...row, page:page, total:data.data.total, commentList:commentList})
        }
      } else {
        delete row.page
        delete row.total
        delete row.commentList
        this.$set(this.clockList, index, row)
      }
    },
    showMoreClock() {
      this.clockPage += 1
      this.getClockRecordList()
    },
    picClick(index, pic) {
      const arr = this.changePic(pic)
      let realArr =[]
      for (let i =0;i<arr.length; i++) {
        if(!arr[i].includes(config.IMG_URL.default)) {
          arr[i] = (`${config.IMG_URL.default}${arr[i]}`)
        }
        if(i<index) {
          arr.push(arr[i])
        } else {
          realArr.push(arr[i])
        }
      }
      this.images = realArr
      const viewer = this.$el.querySelector('.images').$viewer
      viewer.show(0)
    },
    changeImgurl(imgUrl) {
      return changeImgurl(imgUrl, 85, 85)
    },
    async goApply() {
      if(this.data.price > 0) {
        this.$router.push(`/courseOrder/${this.data.courseId}`)
      } else {
        const { data } = await applyFreeCourse(this.data.courseId)
        if( data.code === 0){
          this.$router.push(`/pay/finish/${this.data.courseId}`)
        }
      }
    }
  }
}
