import config from "@/config/common.config"
import { getCourseDetail, collectCourse, getCourseMsg, applyFreeCourse, getHomeTops, courseDownlaod } from "~/api/course"
import ThirdShare from '@/components/Common/ThirdShare'
import CommonContent from '@/components/CourseDetail/CommonContent'
import { countTime, backScrollTop, storage } from 'ais-common-utils'
export default {
  components: { CommonContent, ThirdShare },
  async asyncData(context) {
    const {data} = await getCourseDetail(context.route.params.id)
    if (data.code === 0) {
      data.data.courseId = context.route.params.id
      let livesObj = {}
      data.data.lives.forEach(item => {
        if(!livesObj[item.chapterName]){
          livesObj[item.chapterName] = []
          livesObj[item.chapterName].push(item)
        }else{
          livesObj[item.chapterName].push(item)
        }
      })
      data.data.livesObj = livesObj
      //获取推荐课程
      const res = await getHomeTops()
      if( res.data.code === 0 ) {
        return {
          data: data.data,
          //设置第三方分享参数
          shareOption:{
            title : data.data.name,
            desc: `我已参加这个课程，你也来一起参加吧!`,
            imgUrl: `${config.IMG_URL.default}${data.data.poster}`,
            link: `${config.LINK.school_h5}/courseDetail/${data.data.courseId}`,
            courseId:data.data.courseId
          },
          //设置推荐课程
          tops:res.data.data.tops
        }
      }
    }
  },
  data () {
    return {
      data: {
        lives:[],
        livesObj: {}
      },
      tops:[], //保存推荐会议
      active: 0,
      shareOption:{},
      config,
      currentTime: parseInt((new Date()).getTime()/1000), //获取当前时间
      timer:null, //计时器
      visibleTips: false, //直播报名成功后二维码窗口开关
      initData: false, //获取数据后，渲染界面
      campPage:0
    }
  },
  computed: {
    explain(){
      return (
        this.data.type === 2 ? '系列课' : 
        (this.data.type === 3 ? '训练营': 
          (this.data.lives.length>0 && this.data.lives[0].start && this.data.lives[0].end ? 
            `${this.$changeTime(this.data.lives[0].start)}~${this.$changeTime(this.data.lives[0].end)}直播` : '永久有效') 
        )
      )
    },
    //设置播放按钮状态,以及按钮背景色
    liveStatusTitle(){
      //系列课,单课录播
      if (this.data.type === 2 || this.data.type === 3 || ( this.data.type ===1 && this.data.lives[0].scene === 1 )) {
        //没有报名显示'报名学习';没有购买，显示'购买课程';报名后和购买后，有播放记录显示'继续学习',没有播放记录，显示'立即学习'
        return !this.data.apply ? ( this.data.price === 0 ? { title:'报名学习', style:'join' } : { title:'立即购买', style:'buy' } ) : ( this.data.lastLiveId ? { title:'继续学习', style:'joined' } : { title:'立即学习', style:'join'} )
      }
      //单课直播
      if ( this.data.lives[0].scene !== 1) {
        //购买/报名，显示'观看直播'
        if ( this.data.apply && this.livingStatus === 2) {
          return { title:'观看直播', style:'living' }
        }
        //在直播结束前，未报名的，显示'立即报名'，报名显示'已报名';未购买显示'购买课程'；购买后，显示'暂未开始'
        if( this.livingStatus === 1 || this.livingStatus === 2 ) {
          return this.data.price === 0 ? ( !this.data.apply ? { title:'立即报名', style:'join'} : { title:'已报名', style:'joined' } ) : ( !this.data.apply ? { title:'立即购买', style:'buy' } : { title:'暂未开始', style:'unstart' } )
        }
        //过了播放时间，统一显示已结束
        if( this.livingStatus === 3 ) {
          return { title:'已结束', style:'disabled' }
        }
      }
      return null
    },
    //优惠倒计时
    discountTime() {
      return function(tg) {
        return countTime(this.data.discountEnd*1000, this.currentTime*1000, tg)
      }
    },
    //判断是否优惠
    isDisCount() {
      return this.currentTime>=this.data.discountStart && this.data.discountEnd>=this.currentTime
    },
    //判断直播状态
    //返回1：暂未开始， 2：直播中， 3：已结束
    livingStatus() {
      if(!this.data.lives[0].start && !this.data.lives[0].end) {
        return null
      }
      if(this.data.liveStatus === 1||((this.currentTime > this.data.lives[0].start) && (this.data.lives[0].end > this.currentTime))){
        return 2
      }else if(this.currentTime > this.data.lives[0].end){
        return 3
      }else if((this.data.lives[0].start - this.currentTime)>0){
        return 1
      }
      return null
    },
    //判断课程是否有试听,返回有试听的对象,-1为整节课都试听
    previewItem() {
      if(!this.data.apply){
        const index = this.data.lives.findIndex(p=>((p.previewTime > 0 || p.previewTime === -1)) && p.scene ===1)
        return index > -1 ? { ...this.data.lives[index], previewTime:this.data.lives[index].duration } : false
      }
      return false
    },
    //展示集call按钮
    isCall() {
      return this.data.call > 0 && !this.data.apply && ( this.data.type > 1 || this.livingStatus !== 3)
    },
    //单课课件下载
    singleCourseDownload() {
      return this.data.type === 1 && this.data.lives && this.data.lives.length && this.data.lives[0] && this.data.lives[0].document ? this.data.lives[0].document : false
    }
  },
  mounted () {
    backScrollTop(0)
    this.getCourseMsgHand()
    this.timer = setInterval(() =>{
      this.getCurrentTime()
    }, 1000)
    //把url上推荐人的id保存到本地
    if (this.$route.query.inviterCode) {
      storage.set('inviterObj', {inviterCode:this.$route.query.inviterCode, courseId:this.$route.params.id })
    }
  },
  methods: {
    //登录跳转
    goLogin (url) {
      if (!storage.get('userStatus')) {
        window.location.href = `${config.POST.school_h5}/school/passport/login?mobile=1&state=${url}`
        return false
      }
    },
    jump() {
      const redirect_url = encodeURIComponent(`${window.location.origin}/inviteFriends/${this.data.courseId}?&surname=__NICKNAME__&portrait=__AVATAR__`)
      if(!storage.get('userStatus')) {
        const url = encodeURIComponent(`${config.POST.school_h5}/school/common/mp/authorize?redirect_url=${redirect_url}`)
        window.location.href = `${config.POST.school_h5}/school/passport/login?mobile=1&state=${url}`
        return
      }
      window.location.href = `${config.POST.school_h5}/school/common/mp/authorize?redirect_url=${redirect_url}`
    },
    callClick() {
      const path = `/courseCall/${this.data.call}`
      const url = encodeURIComponent(window.location.origin + path)
      this.goLogin(url)
      this.$router.push({
        path:path
      })
    },
    async hanldeCourseClick () {
      //未购买，点击购买需跳转登录
      if (!this.data.apply) {
        const url = encodeURIComponent(this.$route.query.inviterId ? `${config.LINK.school_pc}/courseDetail/${this.data.courseId}?inviterId=${this.$route.query.inviterId}` : `${config.LINK.school_pc}/courseDetail/${this.data.courseId}`)
        this.goLogin(url)
      }

      //(系列课，单课)
      //已购买或者已申请报名的，单课跳视频播放页；系列课有观看历史的，跳视频播放页，没有则当前页面改变tab到课程目录
      //未报名，调报名接口
      //未购买，弹出确认订单窗口
      if ( this.data.type === 2 || this.data.type === 3 || ( this.data.lives[0].scene === 1 && this.data.type === 1 ) ) {
        if (this.data.apply) { //已购买，已报名
          if ( this.data.type === 2 ||this.data.type === 3 ) { //系列课，有观看历史（lastLiveId不为空）跳视频播放页，否则tab切换到课程目录
            const isExitLiveId = this.data.lives.some(item =>{ //判断lastLiveId是否存在lives数组里
              return item.id === this.data.lastLiveId
            })
            if (isExitLiveId) {
              this.$router.push({ //有观看历史，跳视频播放页
                path:`/videoDetail/${this.data.lastLiveId}`
              })
            } else { //没有观看历史，tab切换到课程目录
              this.$refs.commonContent.changeActive(2)
            }
          } else { //录播直接跳播放页
            this.$router.push({
              path:`/videoDetail/${this.data.lives[0].id}`
            })
          }
        } else if( this.data.price === 0 ){ //免费，未报名调报名接口
          const { data } = await applyFreeCourse(this.data.courseId)
          if( data.code === 0){
            this.$toast.success('报名成功!')
            //报名成功后统一跳转报名成功页
            this.$router.push(`/pay/finish/${this.data.courseId}`)
          }
        } else { //付费，未购买打开付费窗口
          this.$router.push({
            path: this.$route.query.inviterId ? `/courseOrder/${this.data.courseId}?inviterId=${this.$route.query.inviterId}` : `/courseOrder/${this.data.courseId}`
          })
        }
      } else {
        //直播
        //免费：直播未结束前，未报名的，调报名接口；报名后，直播开始前，弹出报名成功二维码，直播中，跳转到直播页面；
        //付费: 直播未结束前，未购买的，弹确认订单窗口；购买后，直播开始前不做操作；直播中，跳转到直播页面
        if( !this.data.apply && ( this.livingStatus === 1 || this.livingStatus === 2)) {
          if (this.data.price ===0 ){ //免费
            const { data } = await applyFreeCourse(this.data.courseId)
            if( data.code === 0){
              this.$toast.success('报名成功!')
              //报名成功后统一跳转报名成功页
              this.$router.push(`/pay/finish/${this.data.courseId}`)
            }
          } else { //收费
            this.$router.push({
              path:this.$route.query.inviterId ? `/courseOrder/${this.data.courseId}?inviterId=${this.$route.query.inviterId}` : `/courseOrder/${this.data.courseId}`
            })
          }
        } else if ( this.data.apply && (this.livingStatus === 2 || this.livingStatus === 1)) {
          if(this.livingStatus === 2 || (900>=(this.data.lives[0].start - this.currentTime))){ //即将开始，直播中可以进去直播间
            this.$router.push({
              path:`/videoDetail/${this.data.lives[0].id}`
            })
          }else {
            this.$toast.fail('讲师还没有开课!')
          }
        }
      }
    },
    //试听点击
    previewClick () {
      this.$router.push({
        path: `/videoDetail/${this.previewItem.id}?isPreview=true`
      })
    },
    //获取课程信息信息
    async getCourseMsgHand(){
      const { data } = await getCourseMsg(this.$route.params.id)
      if(data.code === 0){
        Object.keys(data.data).forEach(key =>{
          this.$set(this.data, key, data.data[key])
          this.initData = true
        })
      }
    },
    // 操作收藏
    async handleCollect () {
      const { data } = await collectCourse(this.data.courseId, !this.data.favorite)
      if(data.code ===0){
        this.$toast(!this.data.favorite ? '收藏成功!' : '取消收藏成功!')
        this.$set(this.data, 'favorite', !this.data.favorite)
      }
    },
    //打开分享窗口
    openShare(){
      this.$refs.ThirdShare.open()
    },
    //获取当前时间
    getCurrentTime() {
      this.currentTime = parseInt((new Date()).getTime()/1000)
    },
    status(item){
      let currentTime = parseInt((new Date()).getTime()/1000), currentItem = {}
      let time = `${this.$changeTime(item.start, 'YYYY-MM-DD HH:mm')}`
      if( currentTime < item.start ){
        currentItem = {
          status:time + (this.data.apply ? '开营' : '待开营'),
          style:'wait',
          type:1
        }
      }else{
        let status = currentTime > item.end
        currentItem = {
          status: time + (status ? '已结束' : '正在开营'),
          style: status ? 'end' : 'bein',
          type:status ? 2 : 3
        }
      }
      return currentItem
    },
    goDetailPeriod(id){
      this.$router.push(`/courseDetail/${id}`)
    },
    movePeriod(){
      this.campPage += 1
    },
    //单课课件下载
    async downLoadCourse () {
      //未登录，点击购买需跳转登录
      const url = encodeURIComponent(`${config.LINK.school_h5}/courseDetail/${this.data.courseId}`)
      this.goLogin(url)
      if (!this.data.apply) {
        return this.data.price === 0 ? this.$toast.fail('请先报名课程!') : this.$router.push({path: `/courseOrder/${this.data.courseId}`})
      }
      
      let {data:{code, data:list}} = await courseDownlaod({courseId:this.data.courseId, liveId :this.data.lives[0].id})
      if(code === 0){
        window.open(list.document)
      }
    }
  },
  head () {
    return {
      title: ((this.data.seo && this.data.seo.title ? this.data.seo.title : this.data.name)) + '—艾思云课堂',
      meta: [
        { hid: 'description', name: 'description', content: (this.data.seo && this.data.seo.description ? this.data.seo.description : this.data.description)},
        { hid: 'Keywords', name: 'Keywords', content: this.data.seo && this.data.seo.keywords ? this.data.seo.keywords : '课程详情' }
      ]
    }
  }
}
